}

.color-gray {
  color: #a7a7a7
}

.color-white {
  color: #f5f5f5
}

.color-blue {
  color: #0062ff
}

.color-red {
  color: #ff1500
}

.color-yellow {
  color: #ffbf00
}

.color-green {
  color: #00b30f
}

.background-black {
  background-color: #111
}

.background-gray {
  background-color: #a7a7a7
}

.background-white {
  background-color: #f5f5f5
}

.background-blue {
  background-color: #0062ff
}

.background-red {
  background-color: #ff1500
}

.background-yellow {
  background-color: #ffbf00
}

.background-green {
  background-color: #00b30f
}

* {
  box-sizing: border-box
}

body,
html {
  margin: 0;
  padding: 0
}

body {
  line-height: 1.6;
  color: #111;
  font-size: 1rem;
  font-weight: 400;
  font-family: Lato, -apple-system, BlinkMacSystemFont, Avenir, Avenir Next, Segoe UI, Roboto, Oxygen, Ubuntu
}

.button,
[type=submit],
button {
  height: 45px;
  margin-bottom: 10px;
  padding: 10px 30px;
  outline: none;
  text-decoration: none;
  color: #f5f5f5;
  font-size: 1rem;
  background: #111;
  border: 1px solid #111;
  border-radius: 0;
  transition: all .2s ease
}

.button:focus,
.button:hover,
[type=submit]:focus,
[type=submit]:hover,
button:focus,
button:hover {
  opacity: .9
}

.button:active,
[type=submit]:active,
button:active {
  opacity: .7
}

.button[disabled],
[type=submit][disabled],
button[disabled] {
  opacity: .8;
  cursor: not-allowed
}

.button.outline,
[type=submit].outline,
button.outline {
  color: #111;
  background: none
}

.button.outline:focus,
.button.outline:hover,
[type=submit].outline:focus,
[type=submit].outline:hover,
button.outline:focus,
button.outline:hover {
  color: #f5f5f5;
  background: #111
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin-top: 0;
  margin-bottom: 10px;
  font-family: Lato, -apple-system, BlinkMacSystemFont, Avenir, Avenir Next, Segoe UI, Roboto, Oxygen, Ubuntu
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 500
}

h1 {
  font-size: 2.986rem;
  line-height: 1.2
}

h2 {
  font-size: 2.488rem;
  line-height: 1.25
}

h3 {
  font-size: 2.074rem;
  line-height: 1.3
}

h4 {
  font-size: 1.728rem;
  line-height: 1.35
}

h5 {
  font-size: 1.44rem;
  line-height: 1.5
}

h6 {
  font-size: 1.2rem;
  line-height: 1.6
}

p {
  font-size: 1rem
}

@media (max-width:768px) {
  h1 {
    font-size: 2.027rem
  }

  h2 {
    font-size: 1.802rem
  }

  h3 {
    font-size: 1.602rem
  }

  h4 {
    font-size: 1.424rem
  }

  h5 {
    font-size: 1.266rem
  }

  h6 {
    font-size: 1.125rem
  }
}

.nav {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  padding-top: 10px;
  padding-bottom: 10px
}

.nav .nav-item {
  margin-right: 10px
}

.nav .nav-item:last-child {
  margin-right: 0
}

.container {
  width: 80%;
  max-width: 960px;
  margin: 0 auto
}

.row {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  width: 100%;
  margin-bottom: 10px
}

.row:last-child {
  margin-bottom: 0
}

.row .col {
  flex-grow: 1;
  flex-basis: 0
}

.row .col,
.row [class*=" col-"],
.row [class^=col-] {
  padding-top: 10px;
  padding-right: 10px;
  padding-left: 10px
}

.row .col-1 {
  width: 8.333333333333332%
}

.row .col-2 {
  width: 16.666666666666664%
}

.row .col-3 {
  width: 25%
}

.row .col-4 {
  width: 33.33333333333333%
}

.row .col-5 {
  width: 41.66666666666667%
}

.row .col-6 {
  width: 50%
}

.row .col-7 {
  width: 58.333333333333336%
}

.row .col-8 {
  width: 66.66666666666666%
}

.row .col-9 {
  width: 75%
}

.row .col-10 {
  width: 83.33333333333334%
}

.row .col-11 {
  width: 91.66666666666666%
}

.row .col-12 {
  width: 100%
}

@media screen and (max-width:768px) {

  .row .col,
  .row [class*=" col-"],
  .row [class^=col-] {
    flex-grow: 0;
    flex-basis: 100%;
    margin-bottom: 10px;
    padding: 0
  }

  .row .col:last-child,
  .row [class*=" col-"]:last-child,
  .row [class^=col-]:last-child {
    margin-bottom: 0
  }
}

input[type=email],
input[type=file],
input[type=number],
input[type=password],
input[type=search],
input[type=tel],
input[type=text],
input[type=url],
select,
textarea {
  height: 45px;
  width: 100%;
  margin-bottom: 10px;
  padding: 10px;
  font-size: 1rem;
  background: #f5f5f5;
  border: 1px solid #a7a7a7;
  border-radius: 0;
  transition: all .2s ease
}

input[type=email]:hover,
input[type=file]:hover,
input[type=number]:hover,
input[type=password]:hover,
input[type=search]:hover,
input[type=tel]:hover,
input[type=text]:hover,
input[type=url]:hover,
select:hover,
textarea:hover {
  border-color: #111
}

input[type=email]:focus,
input[type=file]:focus,
input[type=number]:focus,
input[type=password]:focus,
input[type=search]:focus,
input[type=tel]:focus,
input[type=text]:focus,
input[type=url]:focus,
select:focus,
textarea:focus {
  outline: none;
  border-color: #0062ff
}

textarea {
  min-height: 70px
}

body {
  color: #1e1e1e
}

a {
  text-decoration: none;
  color: #4a4a4a
}

a.join-button {
  display: block;
  color: #fff;
  border-top: 1px solid #fff;
  padding: 1rem 0
}

p {
  color: #9b9b9b;
  line-height: 26px;
  letter-spacing: 1.1px
}

h1 {
  font-weight: 700
}

.button.outline,
button.outline {
  border-color: #e5e5e5;
  font-weight: 700
}

button.outline,
input[type=email],
select,
textarea {
  border-color: #e5e5e5;
  background: transparent;
  margin: 0;
  height: 100%
}

button.outline:focus,
button.outline:hover,
input[type=email]:focus,
input[type=email]:hover,
select:focus,
select:hover,
textarea:focus,
textarea:hover {
  border-color: #e5e5e5
}

select {
  position: relative;
  appearance: none;
  -webkit-appearance: none
}

@media screen and (max-width:768px) {
  .container {
    padding: 0 1rem;
    width: 100%
  }
}

.mt-1 {
  margin-top: .25rem
}

.mt {
  margin-top: .5rem
}

.mt-2 {
  margin-top: 2rem
}

.text-white {
  color: #fff
}

.img-fluid {
  max-width: 100%;
  height: auto
}

@media screen and (max-width:768px) {
  .img-fluid {
    width: 100%
  }
}

@media screen and (max-width:768px) {
  .hide-small {
    display: none
  }
}

.block {
  display: block
}

nav.container {
  width: 100%
}

.logo-section {
  margin-top: .75rem
}

.social-icons a {
  padding-right: 1rem
}

.section-hero {
  margin-top: 8rem
}

.platforms img {
  margin-right: .5rem
}

.double-button {
  display: flex
}

.double-button .button.outline:last-of-type {
  border-left: none
}

.section-crew {
  margin-top: 8rem;
  background-image: url(bg.jpg);
  background-size: cover;
  background-position: 50%;
  background-repeat: no-repeat
}

.section-crew .container {
  padding: 4rem 0
}

@media screen and (max-width:768px) {
  .section-crew .container {
    padding: 4rem 1rem
  }
}

.section-crew .crew-avatars {
  margin-bottom: 3rem
}

.section-crew .crew-avatars .avatar {
  width: 99%;
  min-height: 6rem;
  text-align: center;
  background-color: hsla(0, 0%, 96%, .1);
  margin-right: 1px;
  padding: 2rem
}

.section-crew .crew-avatars .avatar a {
  color: #f5f5f5
}

.section-releases {
  margin-top: 8rem;
  margin-bottom: 8rem
}

.section-contact {
  margin-top: 9rem;
  margin-bottom: 8rem
}

.section-contact .contact-form {
  margin-top: 1rem;
  display: grid;
  grid-template-rows: repeat(6, 1fr);
  grid-template-columns: repeat(12, 1fr)
}

.section-contact .contact-form .email-input {
  grid-column: span 6
}

.section-contact .contact-form .category-select {
  grid-column: span 6;
  border-left: none
}

.section-contact .contact-form .message-input {
  grid-column: span 12;
  grid-row: 2/span 3;
  border-top: none
}

.section-contact .contact-form .form-filler {
  grid-column: span 10;
  border-bottom: 1px solid #e5e5e5;
  border-left: 1px solid #e5e5e5
}

.section-contact .contact-form .submit-button {
  grid-column: span 2;
  border-top: none
}

footer {
  padding: 3rem 0;
  border-top: 1px solid #e5e5e5
}

footer .channels img {
  padding-right: .5rem
}

@media screen and (max-width:768px) {

  footer .text-center,
  footer .text-right {
    text-align: left
  }
}
